.SizesShoppingCart {
    display: flex;
    justify-content: space-between;
    padding-bottom: 25px;
    padding-top: 1em;
    border-bottom: 1px solid #dde3ea;

    .AvailibilityArea {
        font-style: italic;
        font-size: 18px;
        padding: 15px 15px 0;
        .Available {
            color: #129300;
        }
        .NotAvailable {
            color: #ff6000;
        }
    }

    .Sizes .Dimensions {
        font-weight: 100;
        margin-top: 0;
    }
    .Sizes .SizesContent {
        display: flex;
        padding: 5px 25px 0 0;
        box-sizing: border-box;
        justify-content: space-between;
        min-width: 240px;
        color: #555;

        .Key {
            margin-right: 35px;
        }
        .Key, .Value {
            display: inline-block;
        }
    }
    .AddToCartArea .PricesWrp {
        text-align: right;
    }
    .AddToCartArea .Price {
        display: inline-block;
        font-size: 24px;
        padding-bottom: 20px;
        text-align: right;
    }
    .AddToCartArea .PriceCrossed {
        margin-right: 25px;
        position: relative;
        color: #777;
        &::before {
            content: '';
            position: absolute;
            top: 14px;
            left: 0;
            width: 100%;
            height: 3px;
            background: #fb515d;
        }
    }
    .AddToCartArea .AddToCart {
        font-size: 18px;
        border-radius: 23px;
        box-shadow: none;
        font-weight: 400;
        line-height: 36px;
        padding: 0 30px;
        color: #fff;
        border: none;
        background-color: #ff115f;
        cursor: pointer;
        outline: none;
    }
    .ByOneClick {
        color: #555;
        margin-right: 25px;
        font-size: 18px;
        border-radius: 23px;
        box-shadow: none;
        font-weight: 400;
        line-height: 36px;
        padding: 0 30px;
        color: #fff;
        border: none;
        background-color: transparent;
        cursor: pointer;
        outline: none;
        color: #000;
        border: 1px solid;
    
        &:hover {
            color: #39f;
        }
    }
    .CountDownArea {
        display: inline-block;
        transform: translateY(15px);
        margin-left: 20px;
    }
    @media screen and (max-width: 1260px) {
        flex-direction: column-reverse;
        .Sizes {
            margin-top: 25px;
        }
    }
    @media screen and (max-width: 600px) {
        .CountDownArea {
            margin-left: 0;
        }
    }
    @media screen and (max-width: 767px) {
        .AvailibilityArea {
            margin-top: 35px;
            padding-left: 0;
        }
        .AddToCartArea {
            width: 100%;
            .Price {
                padding-bottom: 30px;
            }
            .ByOneClick {
                float: left;
                font-size: 15px;
            }
            .AddToCart {
                float: right;
                font-size: 15px;
            }
        }
    }
    @media screen and (max-width: 365px) {
        .AddToCartArea {
            .ByOneClick {
                padding: 0 20px;
                margin-right: 15px;
            }
            .AddToCart {
                padding: 0 25px;
            }
        }
    }
}